import { useState, MouseEvent } from 'react';
import _isEmpty from 'lodash.isempty';
import {
	Table,
	TableBody,
	TableContainer,
	TableRow,
	Alert,
	Grid,
	TableCell,
	IconButton,
} from '@material-ui/core';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import { generatePath } from 'react-router';
import { useHistory } from 'react-router-dom';
import _get from 'lodash.get';
import { gql, useMutation } from '@apollo/client';
import Header from './Table/Header';
import { TPropertyForm } from '../../../../../types';
import ReviewSellerSubmissionDialog from './Actions/Dialog';
import { PATHS } from '../../../../../routes/path';
import {
	PROPERTY_FORM_UPDATE_TYPE,
	PROPERTY_FORM_CONFIRMATION_MESSAGE,
} from './config';
import graphqlSchema from '../../../../../graphql';
import { useSnackBar } from '../../../../../hooks';
import Loader from '../../../../../ui-component/Loader';
import MenuAction from './Table/MenuAction';

type Props = {
	forms: Array<TPropertyForm>;
	propertyId: string;
	refetch: any;
};

const ReviewSellerSubmissionTable = ({ forms, propertyId, refetch }: Props) => {
	const history = useHistory();
	const [isOpen, setIsOpen] = useState<boolean>(false);
	const [formId, setFormId] = useState<string>('');
	const [type, setType] = useState<number>(0);
	const showSnackBar = useSnackBar();

	const [anchorEl, setAnchorEl] = useState<
		Element | ((element: Element) => Element) | null | undefined
	>(null);

	const handleMenuClick = (
		event: MouseEvent<HTMLButtonElement> | undefined,
		formId: string
	) => {
		setAnchorEl(event?.currentTarget);
		setFormId(formId);
	};

	const handleClose = () => {
		setAnchorEl(null);
	};

	const handleViewForm = () => {
		const formLink = generatePath(PATHS.PROPERTY_FORM, {
			propertyId,
			formId,
		});
		history.push(formLink);
	};

	const handleOpenConfirmation = (type: number) => {
		setType(type);
		setAnchorEl(null);
		setIsOpen(true);
	};

	const handleCloseConfirmation = () => {
		setIsOpen(false);
	};

	const [doApprove, { loading: isApprove }] = useMutation(
		gql(graphqlSchema.approvePropertyForm),
		{
			onCompleted: () => {
				refetch();
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const [doRework, { loading: isRework }] = useMutation(
		gql(graphqlSchema.askReworkPropertyForm),
		{
			onCompleted: () => {
				refetch();
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const message = _get(PROPERTY_FORM_CONFIRMATION_MESSAGE, type, '') as string;

	const onConfirm = () => {
		setIsOpen(false);
		const variables = {
			PropertyId: propertyId,
			FormId: formId,
		};
		if (type === PROPERTY_FORM_UPDATE_TYPE.REQUEST_RESUBMISSION)
			return doRework({ variables });
		return doApprove({ variables });
	};

	if (isRework || isApprove) return <Loader />;

	return (
		<>
			<TableContainer>
				<Table aria-labelledby='tableTitle' size='small'>
					<Header />
					<TableBody>
						{forms.map(form => {
							if (typeof form === 'number') return null;
							return (
								<TableRow key={form.id}>
									<TableCell align='left'>{form.formType}</TableCell>
									<TableCell align='left'>{form.status}</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<IconButton
											onClick={event => {
												handleMenuClick(event, form.id);
											}}
										>
											<MoreHorizIcon />
										</IconButton>
										<MenuAction
											anchorEl={anchorEl}
											handleOpenConfirmation={handleOpenConfirmation}
											handleClose={handleClose}
											handleViewForm={handleViewForm}
										/>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			{_isEmpty(forms) && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
			<ReviewSellerSubmissionDialog
				open={isOpen}
				onClose={handleCloseConfirmation}
				onConfirm={onConfirm}
				message={message}
			/>
		</>
	);
};

export default ReviewSellerSubmissionTable;
