import { Menu, MenuItem } from '@material-ui/core';
import { PROPERTY_FORM_UPDATE_TYPE } from '../config';

type Props = {
	anchorEl: Element | ((element: Element) => Element) | null | undefined;
	handleClose: () => void;
	handleViewForm: () => void;
	handleOpenConfirmation: (type: number) => void;
};

const MenuAction = ({
	anchorEl,
	handleClose,
	handleViewForm,
	handleOpenConfirmation,
}: Props) => (
	<Menu
		anchorEl={anchorEl}
		open={Boolean(anchorEl)}
		onClose={handleClose}
		sx={{
			'& .MuiMenu-paper': {
				boxShadow: 1,
			},
		}}
	>
		<MenuItem onClick={handleViewForm}>View Progress</MenuItem>
		<MenuItem
			onClick={() =>
				handleOpenConfirmation(PROPERTY_FORM_UPDATE_TYPE.REQUEST_RESUBMISSION)
			}
		>
			Request Resubmission
		</MenuItem>
		<MenuItem
			onClick={() => handleOpenConfirmation(PROPERTY_FORM_UPDATE_TYPE.OTHER)}
		>
			Approve
		</MenuItem>
	</Menu>
);

export default MenuAction;
